.CounDownContainer{
    display: flex;
    flex-direction: row;
    align-items: baseline;
    justify-content: center;
}
.containercounterdown{
    
    padding: 0;
    margin: 0;
}
.containercounterdown>h1{
    color: #ddb480;
    font-family: 'Playfair Display SC', serif;

}
.Numero{
    font-size: 120px;
    padding: 5px;
    color: #494a4a;
}
.tipodemedida{
    font-family: 'Dancing Script', 'cursive';
    font-size: 30px;
    color:#494a4a;

}
.container-confirmacion p{
    padding-left: 10px;
}
@media (max-width:845px) {
    .Numero{
        font-size: 50px;
        padding: 5px;
        color: #494a4a;
    }
    .tipodemedida{
        font-family: 'Dancing Script', 'cursive';
        font-size: 15px;
        color:#494a4a;
    
    }
    }