.nav {

  width: 100%;
  position: relative;
  border-bottom:solid 1px #684a41;
  display: flex;
  flex-direction: row;
  color: #4a4a4c;
}

.nav > .nav-header {
  display: inline;
}

.nav > .nav-header > .nav-title {
  display: inline-block;
  
  
}
.nav-title{
  float: left;
  font-size: 22px;
  color: #fff;
  padding: 0px 20% 10px 10px;
}

.nav > .nav-btn {
  display: none;
}

.nav > .nav-links {
  display: inline;
  float:left;
  font-size: 18px;
  justify-content: center;
  margin-left: 10%;
}

.nav > .nav-links > a {
  display: inline-block;
  padding: 13px 0px 0px 15px;
  text-decoration: none;
  color: #684a41;
}

.nav > .nav-links > a:hover {
  color: white;
}

.nav > #nav-check {
  display: none;
}

@media (max-width:845px) {
  .nav > .nav-btn {
    display: inline-block;
    position: absolute;
    right: 0px;
    top: 0px;
  }
  .nav > .nav-btn > label {
    display: inline-block;
    width: 50px;
    height: 50px;
    padding: 13px;
  }
  .nav > .nav-btn > label:hover,.nav  #nav-check:checked ~ .nav-btn > label {
    background-color: rgba(0, 0, 0, 0.3);
  }
  .nav > .nav-btn > label > span {
    display: block;
    width: 25px;
    height: 10px;
    border-top: 2px solid #983f26;
  }
  .nav > .nav-links {
    position: absolute;
    display: block;
    width: 90%;
    background-color: #333;
    height: 0px;
    transition: all 0.3s ease-in;
    overflow-y: hidden;
  
    left: 0px;
  }
  .nav > .nav-links > a {
    display: block;
    width: 70%;
  }
  .nav > #nav-check:not(:checked) ~ .nav-links {
    height: 0px;
  }
  .nav > #nav-check:checked ~ .nav-links {
    height: calc(100vh - 50px);
    overflow-y: auto;
  }
}
@media (max-width:845px) {
.imgLogo{
  margin-top: 100px;
}
.IconNyF{
  display: none;
}
}